export default {
  name: "Draw2D",
  data() {
    return {
      canvas: null
    };
  },
  methods: {
    addnew() {
      const label = new draw2d.shape.basic.Label({
        text: "點我改名",
        stroke: 0
      });
      label.installEditor(new draw2d.ui.LabelInplaceEditor());
      const figure = new draw2d.shape.node.Between({
        bgColor: new draw2d.util.Color("#ffffff00"),
        width: 120,
        height: 40
      });
      figure.add(label, new draw2d.layout.locator.CenterLocator());
      this.canvas.add(
        figure,
        Math.random() * 100 + 50,
        Math.random() * 100 + 200
      );
    }
  },
  mounted() {
    const canvas = new draw2d.Canvas("draw2d");
    this.canvas = canvas;
    canvas.installEditPolicy(
      new draw2d.policy.canvas.FadeoutDecorationPolicy()
    );
    var createConnection = function() {
      var con = new draw2d.Connection({
        radius: 1,
        router: new draw2d.layout.connection.InteractiveManhattanConnectionRouter()
      });
      con.setTargetDecorator(new draw2d.decoration.connection.ArrowDecorator());
      return con;
    };
    canvas.installEditPolicy(
      new draw2d.policy.connection.DragConnectionCreatePolicy({
        createConnection: createConnection
      })
    );

    canvas.on("select", function(emitter, event) {
      // console.log(emitter);
